<template>
  <div id="receive">
    <div class="header">
      <h1>如何完美收花</h1>
      <p>2015-06-15 花点时间的操心人</p>
    </div>
    <ul>
      <li>
        <img src="https://img.yzcdn.cn/upload_files/2016/03/04/FqkHg_M5EZUFbTMsjqG1JAVuvC6P.jpg!730x0.jpg">
      </li>
      <li>
        <img src="https://img.yzcdn.cn/upload_files/2016/03/04/FpQYTzxT62x4xe1q8rdE_Mld6PSE.jpg!730x0.jpg">
      </li>
      <li>
        <img src="https://img.yzcdn.cn/upload_files/2016/03/04/FnAC33uxgNPQW3C5VQCfCes2nkiG.jpg?imageView2/2/w/730/h/0/q/75/format/jpg" >
      </li>
      <li>
        <img src="https://img.yzcdn.cn/upload_files/2016/06/02/FjkdgJ4E7mmdtG__9qafrNZ-zcQ8.jpg?imageView2/2/w/730/h/0/q/75/format/jpg" >
      </li>
    </ul>
    <div class="foot_hide" style="height:2rem"></div>
		<footerdiv></footerdiv>
  </div>
</template>
<script>
	import footerdiv from '../footer/footer'
export default {
  name: "receive",
  components: {
		footerdiv
	},
  data () {
    return {
      isHide1:false,
      isHide2:false,
      isHide3:false
    }
  },
  methods:{
    chooseflower(item){
      this.$router.push({path: '/flowers/'+item.name})
    },
    hide1(){
      this.isHide1 = !this.isHide1;
      this.isHide2 = false;
      this.isHide3 = false;
    },
    hide2(){
      this.isHide2 = !this.isHide2;
      this.isHide1 = false;
      this.isHide3 = false;
    },
    hide3(){
      this.isHide3 = !this.isHide3;
      this.isHide1 = false;
      this.isHide2 = false;
    }
  }
}
</script>
<style scoped>
#receive{
  background: #f9f9f9;
}
.header{
  padding: 0.7rem 0.4rem;
}
.header p{
  color: #8c8c8c;
}
ul{
  padding: 0.4rem;
}
img{
  max-width: 100% !important;
  width: auto !important;
}
</style>



